import React, { useEffect } from "react";
import '../../assets/css/Home.css';
import { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import 'animate.css'
import TrackVisibility from "react-on-screen";
import MyHeaderImg from "../../assets/image/header.png";
import WhatsappImg from "../../assets/image/icons8-whatsapp-100.png";
import LinkedinImg from "../../assets/image/icons8-linkedin-100.png";
import InstagramImg from "../../assets/image/icons8-instagram-100.png";
import { WindupChildren } from "windups";

function Home(){
    

    return(    
        <section className="HomeMenu" id="home">
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7}>
                    <TrackVisibility>
                        {({isVisible})=>
                        <div className={isVisible? "animate__animated animate__fadeIn" : ""}>
                            <span className="tagline">
                            Welcome, I'm
                            </span>
                            <h1>
                                
                                {`Nanda Hayatun Nufus`}
                                <span>
                                <span className="wrap"></span>
                                <p>A hardworking and enthusiastic person in Web Development with 2+ years of experince successfully in designing, developing and maintain Web Apps.</p>
                                </span>
                                <WindupChildren>
                                    <div className="jobtagline">
                                    {"Web Developer - PHP and Javascript based"}
                                    </div>
                                    <div>
                                        <a href="https://wa.me/6281229962428?text=Hello.%20It's%20Nanda's%20Whatsapp%20number.%20Feel%20free%20to%20ask%20important%20message%20to%20me.%20Thank%20You!" target="_blank" rel="noreferrer">
                                        <img src={WhatsappImg} alt="Nanda Whatsapp"/>
                                        </a>
                                        <a href="https://www.linkedin.com/in/nanda-hayatun-nufus-a99686121/" target="_blank" rel="noreferrer">
                                        <img src={LinkedinImg} alt="Nanda Linkedin"/>
                                        </a>
                                        <a href="https://www.instagram.com/nandahayatun_nufus/" target="_blank" rel="noreferrer">
                                        <img src={InstagramImg} alt="Nanda Instagram"/>
                                        </a>
                                    </div>
                                </WindupChildren>
                            </h1>
                        </div>
                        }
                    </TrackVisibility>
                    </Col>
                    <Col xs={12} md={6} xl={5}>
                    <TrackVisibility>
                        {({isVisible})=>
                        <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                            <img src={MyHeaderImg} alt="Nanda Header Img"/>
                        </div>
                        }
                    </TrackVisibility>
                    </Col>
                </Row>
            </Container>
        </section>
        
    );
}
export default Home;