import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import TrackVisibility from "react-on-screen";
import ExperienceCSS from "../../assets/css/Experience.css";
import ITI from "../../assets/image/ITI-logo.png";
import MITE from "../../assets/image/MITE-LOGO.png";

function Experience(){
    return(
        <section className="Experience" id="experience">
            <Container>
            <div style={{ marginTop: "100px" , marginBottom: "20px"}}>
                <h1>Work Experience</h1>
                <Row>
                    <Col xs={12} md={6} xl={5}>
                        <TrackVisibility>
                            {({isVisible})=>
                            <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                                <img src={ITI} alt="ITI Logo"/>
                            </div>
                            }
                        </TrackVisibility>
                    </Col>    
                    <Col xs={12} md={6} xl={7}>
                        <TrackVisibility>
                            {({isVisible})=>
                            <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                                <div className="description">
                                <div className="fw-bold" align="left">INSTITUT TEKNOLOGI INDONESIA</div>
                                <div className="fw-bold" align="left">Laboratory Assistant</div>
                                    <ol type="1">
                                        <li>Teach step by step using logic flowchart for new programming task</li>
                                        <li>Teach step by step how to make Entity Relation(ER) diagram with SQL Server</li>
                                        <li>Teach Informatics college studet on practical programming classes such as C++, PHP, VB.net and Database learning</li>
                                    </ol>
                                </div>
                            </div>
                            }
                        </TrackVisibility>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={6} xl={5}>
                        <TrackVisibility>
                            {({isVisible})=>
                            <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                                <img src={MITE} alt="MIT-E Logo"/>
                            </div>
                            }
                        </TrackVisibility>
                    </Col>    
                    <Col xs={12} md={6} xl={7}>
                        <TrackVisibility>
                            {({isVisible})=>
                            <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                                <div className="description">
                                <div className="fw-bold" align="left">PT MITRA ELANG NIAGA INDONESIA</div>
                                <div className="fw-bold" align="left">IT Staff</div>
                                    <ol type="1">
                                        <li>Designed, developed and maintain user-friendly web application features using Agile Scrum method</li>
                                        <li>Experience with HTML, PHP, Javascript and MySQL</li>
                                        <li>Developed Invoice Flow, Deposite System, Petty Cash and Big Cash for finance used</li>
                                        <li>Experience with remote access tool to server using linux command in SecureCRT that connect to OpenVPN</li>
                                        <li>Create and Implement integrated APIs feature into web apps using SOAP and JSON</li>
                                        <li>Trained staff members in internal web functions, including step on how to independenly make minor update and changes</li>
                                    </ol>
                                </div>
                            </div>
                            }
                        </TrackVisibility>
                    </Col>
                   
                </Row>
            </div>
            </Container>
        </section>
    );
}
export default Experience;