import React from "react";
import SkillCSS from "../../assets/css/Skills.css";
import MysqlIcon from "../../assets/image/mysql.png";
import JSIcon from "../../assets/image/js.png";
import APIicon from "../../assets/image/api-300x300.png";
import PHPicon from "../../assets/image/php.png";
import BTicon from "../../assets/image/bootstrap.png";
import Reacticon from "../../assets/image/react.png";
import Laravelicon from "../../assets/image/laravellogo.png";
import { Col, Container, Row } from "react-bootstrap";
import 'animate.css'
import TrackVisibility from "react-on-screen";

function Skills(){
    return(
        <section className="Skills" id="skills">
            <Container>
            <h1>Skills Expertise</h1>
            <Row>
             <Col  xs={12} md={12} xl={12}>
               <div className="skillCard animate__animated animate__fadeInLeftBig">
               <div className="card">
                    <div className="card-img">
                        <img src={PHPicon} alt="" />
                        <h3 className="card-title">
                            PHP
                        </h3>
                    </div>
                    <div className="card-content">
                        Expertise in Native PHP
                    </div>
                </div>
                <div className="card">
                    <div className="card-img">
                        <img src={JSIcon} alt="" />
                        <h3 className="card-title">
                            Javascript
                        </h3>
                    </div>
                    <div className="card-content">
                        Expertise in Native Javascript
                    </div>
                </div>
                <div className="card">
                    <div className="card-img">
                        <img src={BTicon} alt="" />
                        <h3 className="card-title">
                            Bootstrap
                        </h3>
                    </div>
                    <div className="card-content">
                        Expertise in Bootstrap 4
                    </div>
                </div>
                <div className="card">
                    <div className="card-img">
                        <img src={MysqlIcon} alt="" />
                        <h3 className="card-title">
                            MySQL
                        </h3>
                    </div>
                    <div className="card-content">
                        Expertise in MySQL Database with remote method
                    </div>
                </div>
                <div className="card">
                    <div className="card-img">
                        <img src={APIicon} alt="" />
                        <h3 className="card-title">
                            API
                        </h3>
                    </div>
                    <div className="card-content">
                        Expertise in SOAP and JSON API
                    </div>
                </div>
                </div>
               </Col>
            </Row>
            <h1>Skills Basic</h1>
            <Row>
             <Col  xs={12} md={12} xl={12}>
               <div className="skillCard">
               <div className="card">
                    <div className="card-img">
                        <img src={Laravelicon} alt="" />
                        <h3 className="card-title">
                            Laravel
                        </h3>
                    </div>
                    <div className="card-content">
                        Have Basic with installation ver 8, Route, Blade and Controller
                    </div>
                </div>
                <div className="card">
                    <div className="card-img">
                        <img src={Reacticon} alt="" />
                        <h3 className="card-title">
                            React JS
                        </h3>
                    </div>
                    <div className="card-content">
                        Have Basic with installation ver16+, Props, Hooks, State, and RestAPI
                    </div>
                </div>
                </div>
              </Col>
            </Row>
            </Container>
        </section>
    );
}

export default Skills;